	module.exports = {"data":{"markdownRemark":{"html":"<p>1994年には、騒々しい忙しい中を過ぎ去ろうとしていました。このような雰囲気の中で、何年も過ぎました。暖かく色とりどりの社会の変化に伴い、人々の主観性も変化し、環境は人を変え、人々も環境を変えました。今日中国に住んでいるのは、おそらく個人の主観性が変わる可能性が最も高い期間です。中国の社会は歴史の中で最も深刻な変化を遂げてきたので、中国の環境はかつてないほどの変化を遂げました。</p>\n<p>最も深い感覚は、人生が多く消費されるということです。この人生の明確な意識は、おそらく老後の危機感に起因すると考えられます。私は10代の年には生命感がないようですが、すべてが水に費やされています。水泳のときに池の中で溺れてしまいました。今日、人生の意識はますます強くなり、人間が逃げることのできない論理かもしれない。</p>\n<p>時には私は人の最大の恐怖は生命意識から来ていると思います。意識のない人生の恐れはありません。人生の存在は人間恐怖の最後の状態です。</p>\n<p>同時に、人の最大の幸福は人生から来ています。人生がなければ、幸福はありません。人生の存在は、人間の幸福の最後の条件です。</p>\n<p>この世界に住むすべての人々は、この2つの条件の間でバランスをとることを積極的またはパッシブに探したいので、人々のさまざまな部門があります。幸福か恐れ、生命は常に失われる。人生の喪失の過程で自分の理想と意志を実現し、人々の選択を完了することは貴重な人生です。歴史は過去1年と1年であり、人生は日々減少しています。</p>\n<p>これは個人にも当てはまりますが、人類全体では歴史が年々伸び、人生は日々増えています。</p>\n<p>個体は全体的に減少し、個体減少で全体が増加する。私たちは自分自身と社会を理解し、この理解を残すことで、私たちが持つべき幸福を減らすことができます。 （終了）</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0199.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0199.md","frontmatter":{"title":"12月1日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0199.md absPath of file >>> MarkdownRemark"}}

